import React from 'react';
import PropTypes from 'prop-types';
import Typography from './../Typography';
import { getSpacing, useStyles } from './../system';
import { stylesPropType } from './../utils/propTypes';

function getStyles() {
	return {
		root: {
			display: 'flex',
			alignItems: 'center',
			...getSpacing({
				py: 3,
				px: [3, 4],
			}),
		},
		avatar: {
			flex: '0 0 auto',
			...getSpacing({
				mr: 3,
			}),
		},
		action: {
			flex: '0 0 auto',
			alignSelf: 'flex-start',
			...getSpacing({
				mt: -2,
				mr: [-3, -4],
			}),
		},
		content: {
			flex: '1 1 auto',
		},
		title: {},
		subheader: {},
	};
}
getStyles.propTypes = getSpacing.propTypes;

function CardTitle(props) {
	const { avatar, className, disableTypography, title, ...passThru } = props;

	if (
		title &&
		!disableTypography &&
		(!title.type || (title.type && title.type !== Typography))
	) {
		return (
			<Typography
				as="span"
				className={className}
				variant={avatar ? 'body2' : 'headline'}
				{...passThru}
			>
				{title}
			</Typography>
		);
	} else if (title) {
		return title;
	}
	return null;
}

function CardSubHeader(props) {
	const {
		avatar,
		className,
		disableTypography,
		subheader,
		...passThru
	} = props;

	if (
		subheader &&
		!disableTypography &&
		(subheader.type && subheader.type !== Typography)
	) {
		return (
			<Typography
				as="span"
				className={className}
				color="text.secondary"
				variant={avatar ? 'body2' : 'body1'}
				{...passThru}
			>
				{subheader}
			</Typography>
		);
	} else if (subheader) {
		return subheader;
	}
	return null;
}

function CardHeader(props) {
	const [
		{ classes },
		{
			action,
			as: Component,
			avatar,
			className,
			disableTypography,
			subheader,
			subheaderTypographyProps,
			title,
			titleTypographyProps,
			...passThru
		},
	] = useStyles(props, getStyles);

	return (
		<Component className={classes.root} {...passThru}>
			{avatar && <div className={classes.avatar}>{avatar}</div>}
			<div className={classes.content}>
				<CardTitle
					avatar={avatar}
					className={classes.title}
					disableTypography={disableTypography}
					title={title}
					titleProps={titleTypographyProps}
				/>
				<CardSubHeader
					avatar={avatar}
					className={classes.subheader}
					disableTypography={disableTypography}
					subheader={subheader}
					subheaderProps={subheaderTypographyProps}
				/>
			</div>
			{action && <div className={classes.action}>{action}</div>}
		</Component>
	);
}

CardHeader.displayName = 'CardHeader';

CardHeader.propTypes = {
	// The action to display in the card header.
	action: PropTypes.node,
	/**
	 * The component used for the root node.
	 * Either a string to use a DOM element or a component.
	 */
	as: PropTypes.oneOfType([
		PropTypes.string,
		PropTypes.func,
		PropTypes.object,
	]),
	// The Avatar for the Card Header.
	avatar: PropTypes.node,
	className: PropTypes.string,
	/**
	 * If `true`, the children won't be wrapped by a Typography component.
	 * This can be useful to render an alternative Typography variant by
	 * wrapping
	 * the `title` text, and optional `subheader` text
	 * with the Typography component.
	 */
	disableTypography: PropTypes.bool,
	// The content of the component.
	subheader: PropTypes.node,
	/**
	 * These props will be forwarded to the subheader
	 * (as long as disableTypography is not `true`).
	 */
	subheaderTypographyProps: PropTypes.object,
	// The content of the Card Title.
	title: PropTypes.node,
	/**
	 * These props will be forwarded to the title
	 * (as long as disableTypography is not `true`).
	 */
	titleTypographyProps: PropTypes.object,
	...stylesPropType,
};

CardHeader.defaultProps = {
	as: 'div',
	disableTypography: false,
};

export default CardHeader;



